@font-face {
    font-family: 'OpenSansBold';
    src: url('@{base-font-path}/opensans-bold-webfont.eot');
    src: url('@{base-font-path}/opensans-bold-webfont.eot?#iefix') format('embedded-opentype'),
    url('@{base-font-path}/opensans-bold-webfont.woff2') format('woff2'),
    url('@{base-font-path}/opensans-bold-webfont.woff') format('woff'),
    url('@{base-font-path}/opensans-bold-webfont.ttf') format('truetype'),
    url('@{base-font-path}/opensans-bold-webfont.svg#open_sansbold') format('svg');
    font-weight: normal;
    font-style: normal;
}
