body.modal-open {
    overflow: hidden;
}
.modal-backdrop {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    background: #727070;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=72)";
    opacity: 0.72;
}
.modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1010;
    opacity: 1;
    overflow-y: auto;
}
.modal-dialog {
    position: relative;
    width: 362px;
    margin: 50px auto 0;
    opacity: 1;
    z-index: 1020;
}
.modal-title {
    padding: 3px 20px;
    font-family: OpenSansLight, Arial, sans-serif;
    font-size: 12px;
    color: #f8f7f6;
}
.modal-body {
    position: relative;
    padding: 20px 20px 25px;
    border: 1px solid #bababa;
    border-radius: 5px;
    background: #f5f4f3;
    box-shadow: 0px 11px 27px 0px rgba(159, 159, 159, 0.75), inset 0 6px 6px 0px rgba(255, 255, 255, 1);
}
.modal-close {
    position: absolute;
    top: 6px;
    right: 6px;
    margin: 0;
    padding: 0;
    width: 14px;
    height: 14px;
    border: none;
    background: transparent;
}
.icon-close {
    display: block;
    width: 14px;
    height: 14px;
    background: url(../img/icon-close.png) no-repeat;
}
.modal-form .form-group {
    margin-bottom: 8px;
    font-size: 14px;
}
.modal-form .form-group:last-child {
    margin-bottom: 0;
}
.modal-form .control-label {
    margin-bottom: 7px;
}
.modal-form .form-control,
.modal-form .form-textarea {
    background: rgba(251,252,253,1);
    background: -moz-linear-gradient(top, rgba(251,252,253,1) 0%, rgba(244,243,244,1) 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(251,252,253,1)), color-stop(100%, rgba(244,243,244,1)));
    background: -webkit-linear-gradient(top, rgba(251,252,253,1) 0%, rgba(244,243,244,1) 100%);
    background: -o-linear-gradient(top, rgba(251,252,253,1) 0%, rgba(244,243,244,1) 100%);
    background: -ms-linear-gradient(top, rgba(251,252,253,1) 0%, rgba(244,243,244,1) 100%);
    background: linear-gradient(to bottom, rgba(251,252,253,1) 0%, rgba(244,243,244,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fbfcfd', endColorstr='#f4f3f4', GradientType=0 );
}
.modal-form .form-textarea {
    height: 110px;
    resize: none;
}
.modal-form .form-group-description {
    margin-bottom: 13px;
}
.modal-form .btn-group .btn {
    min-width: 123px;
    margin-left: 90px;
}
.fileinput-group {
    position: relative;
    display: table;
    /*border-collapse: separate;*/
    width: 100%;
}
.fileinput-group .form-control {
    position: relative;
    display: table-cell;
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    width: auto\9 !important;
    border: 1px solid #d6d5d3\9 !important;
}
.fileinput-group .form-control:focus {
    border-color: #66a286 !important;
}
.fileupload-wrapper {
    position: relative;
    display: table-cell;
    width: 1%;
    white-space: nowrap;
    vertical-align: middle;
    width: 52px;
    height: 41px;
    border: 1px solid #d6d5d3;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    box-shadow: 0px 2px 1px 0px rgba(9, 9, 9, 0.07), inset 0px -2px 3px 0px rgba(41, 41, 41, 0.05);
    background: rgba(252,252,252,1);
    background: -moz-linear-gradient(top, rgba(252,252,252,1) 0%, rgba(233,230,230,1) 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(252,252,252,1)), color-stop(100%, rgba(233,230,230,1)));
    background: -webkit-linear-gradient(top, rgba(252,252,252,1) 0%, rgba(233,230,230,1) 100%);
    background: -o-linear-gradient(top, rgba(252,252,252,1) 0%, rgba(233,230,230,1) 100%);
    background: -ms-linear-gradient(top, rgba(252,252,252,1) 0%, rgba(233,230,230,1) 100%);
    background: linear-gradient(to bottom, rgba(252,252,252,1) 0%, rgba(233,230,230,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fcfcfc', endColorstr='#e9e6e6', GradientType=0 );
    cursor: pointer;
}
.fileupload-wrapper .icon-cloud {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -10px;
    margin-left: -12px;
    width: 25px;
    height: 18px;
    background: url(../img/spritesheet.png) -57px -5px;
}
.fileupload {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}
